.outerWrapper{
    width: 100%;
    height: auto !important;
    display: flex;
    clear:both;
    overflow: hidden;
}

.rightWrapper, .leftWrapper{
    width: 50%;
    max-width: 50%;
    min-height: 100%;
    height: auto !important;
    text-align: left;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.listTitle{
    font-weight: bold;
}

.listWrapper{
    display: flex;
    flex-wrap: wrap;
}

.droppableSubjectCard{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fee08e;
    margin: 3px;
    font-size: 13pt;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid #efce79;
    color: black;
    width: max-content;
}

.auswahlWrapper{
    width: 100%;
    min-height: 15%;
    background-color: #2c3443;
    border-radius: 7px;
    display: flex;
    flex-direction: column;
    text-align: center;
    margin-bottom: 10px;
    color: white;
}

.auswahl {
    width: 100%;
    min-height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.droppableSubjectCardAuswahl{
    width: 80%;
    border-radius: 5px;
    border: 1px solid gray;
    padding: 10px;
    margin: 7px;
    text-align: center;
    height: min-content;
    background-color: rgba(239, 88, 136, 0.661);
}

.oral{
    background-color: rgba(226, 123, 123, 0.561);
}

.basis{
    background-color: rgba(52, 232, 160, 0.561);
}

.choose{
    background-color: rgba(241, 241, 111, 0.535);
}

.infoContainer{
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 5px;
}

.infoIcon{
    color: black;
    font-size: 35px;
    background-color: white;
    padding: 5px;
    border-radius: 25px;
    border: 2px solid;
}

.alertSpace .ant-alert-error {
    background-color: rgba(255, 0, 0, 1) !important;
}

.alertSpace{
    position: absolute;
    top: 20px;
    right: 20px;
    max-width: 300px;
}

.downloadButton{
    width: 200px;
    margin: 10px;
}