.stepWrapper{
    width: 100%;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.title{
    color: white;
    font-weight: bolder;
}

.stepContent{
    width: 100%;
    max-width: 750px;
    min-height: 200px;
    text-align: center;
    background-color: #1b1f27;
    border-radius: 0 0 10px 10px;
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-direction: column;
    color: white;
}

.steps{
    max-width: 750px;
    background-color: #263038;
    color: white;
    padding: 15px 15px 10px 15px;
    border-radius: 10px 10px 0 0;
}

.ant-steps-item-title{
    background-color: transparent !important;
    color: white !important;
}

.ant-steps-item-wait .ant-steps-icon{
    color: #97999e !important;
}

.buttonWrapper {
    width: 100%;
    max-width: 750px;
    justify-content: right;
    justify-content: space-between;
    align-items: center;
    display: flex;
}

.nextButton, .previousButton {
    width: auto;
    height: auto;
    background-color: transparent;
    box-shadow: none;
    border: none;
}

.nextButton:hover, .previousButton:hover{
    background-color: transparent !important;
    transform: scale(1.1);
}

.nextButtonArrow, .previousButtonArrow{
    font-size: 50px;
    color: rgb(78, 76, 76);
}