.subjectCardWrapper{
    height: auto;
    width: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.subjectCard{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fee08e;
    color: black;
    margin: 3px;
    font-size: 11pt;
    padding: 0px 5px 0px 5px;
    border-radius: 5px;
    border: 1px solid #efce79;
    font-weight: bold;
}

.discardSubjectButton{
    margin-left: 5px;
    background-color: transparent;
    border: none;
    font-size: 20pt;
    padding: 0px;
    font-weight: bolder;
    color: rgb(255, 0, 0);
    display: flex;
    align-items: center;
}

.discardSubjectButton:hover{
    color:rgb(255, 0, 0) !important;
}

.discardSubjectIcon{
    margin: 0px;
}

.sectionTitle{
    padding: 5px;
}